<template>
  <div class="d-flex flex-column pt-6 pb-2 px-0">
    <ItemPageLegendItem v-if="!complete" status="not-filled" class="mb-3" />
    <ItemPageLegendItem
      class="mb-6"
      v-if="!complete"
      status="not-fully-filled"
    />

    <VCheckbox v-if="complete" v-model="agreement" class="mb-3">
      <VTypography font-style="text-2" class="v-checkbox__text">
        Нажимая кнопку «Отправить на проверку» вы соглашаетесь на
        <a class="v-checkbox__link" target="_blank" href="https://vk.com"
          >обработку персональных данных</a
        >
        и подтверждаете, что ознакомились с
        <a class="v-checkbox__link" target="_blank" href="https://vk.com"
          >положением Сколтеха</a
        >.
      </VTypography>
    </VCheckbox>

    <VPopover
      :disabled="!disabled"
      trigger-type="hover"
      position="top-center"
      class="item-page-footer__popover"
    >
      <template v-slot:content>
        <ActionButton
          :disabled="disabled"
          @click="$emit('send')"
          button-type="secondary-outline"
          class="item-page-footer_submit-button"
          >Отправить на проверку</ActionButton
        >
      </template>

      <template v-slot:popover>
        <PopoverContent :width="234">
          <VTypography
            >Кнопка отправки будет доступна после заполнения всех
            вкладок</VTypography
          >
        </PopoverContent>
      </template>
    </VPopover>
  </div>
</template>

<script>
import VCheckbox from "@/components/ui/Checkbox";
import VPopover from "@/components/popover/Popover";
import VTypography from "@/components/ui/Typography";
import ActionButton from "@/components/buttons/ActionButton";
import PopoverContent from "@/components/popover/PopoverContent";
import ItemPageLegendItem from "@/components/item-page/ItemPageLegendItem";

export default {
  name: "ItemPageFooter",
  components: {
    PopoverContent,
    VPopover,
    VCheckbox,
    ActionButton,
    ItemPageLegendItem,
    VTypography,
  },
  emits: {
    send: Function,
  },
  props: {
    complete: Boolean,
  },
  data() {
    return {
      agreement: false,
    };
  },
  computed: {
    disabled() {
      return !this.complete || !this.agreement;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.item-page-footer_submit-button {
  max-width: 234px;
}

.v-checkbox__link {
  color: $gray1;
}

.item-page-footer__popover {
  width: 234px;
}
</style>
