<template>
  <div class="d-flex w-100 v-tabs">
    <TabsItem
      :key="item.key"
      :title="item.title"
      v-for="item in items"
      :status="item.status"
      :form-status="status"
      @visit="onVisit(item.key)"
      :active="currentTab === item.key"
      @click="$emit('change', item.key)"
      :visited="checkKeyVisited(item.key)"
    />
  </div>
</template>

<script>
import TabsItem from "@/components/tabs/TabsItem";
import { mapGetters } from "vuex";

export default {
  name: "VTabs",
  components: { TabsItem },
  emits: {
    change: Function,
  },
  props: {
    currentTab: String,
    items: {
      type: Array,
      default() {
        return [];
      },
      validator(value) {
        return value.every((x) => !!x.key && !!x.title);
      },
    },
  },
  data() {
    return {
      visitedTabs: [],
    };
  },
  methods: {
    onVisit(key) {
      if (!this.visitedTabs.includes(key)) {
        const list = this.visitedTabs.slice();

        list.push(key);

        this.visitedTabs = list;
      }
    },
  },
  computed: {
    ...mapGetters("formInfo", {
      status: "getStatus",
    }),
    checkKeyVisited() {
      return (key) => {
        return this.visitedTabs.includes(key);
      };
    },
  },
};
</script>

<style scoped lang="scss">
.v-tabs {
  overflow-x: auto;

  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: none;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.25);
    outline: 1px solid rgba(0, 0, 0, 0.25);
  }
}
</style>
