<template>
  <table class="v-table">
    <thead>
      <tr class="v-table__row v-table__header-row">
        <th class="v-table__cell v-table__auto-width">№</th>
        <th class="v-table__cell">Название проекта</th>
        <th class="v-table__cell v-table__auto-width">Номер проекта</th>
        <th class="v-table__cell v-table__auto-width">Дата договора</th>
        <th class="v-table__cell v-table__auto-width">Номер договора</th>
        <th class="v-table__cell">Полное наименование лица</th>
        <th class="v-table__cell v-table__auto-width">Действия</th>
      </tr>
    </thead>

    <tbody>
      <tr
        :key="index"
        v-for="(item, index) in resources"
        class="v-table__row v-table__body-row"
      >
        <td class="v-table__cell text-center">
          {{ index + 1 }}
        </td>
        <td
          class="v-table__cell"
          :class="{
            'resources-external-table__name-cell--edit-hover':
              editIndexHover === index,
            'resources-external-table__name-cell--delete-hover':
              deleteIndexHover === index,
          }"
        >
          {{ item.russianName }}
        </td>
        <td class="v-table__cell">
          {{ item.projectNumber }}
        </td>
        <td class="v-table__cell">
          {{ item.date }}
        </td>
        <td class="v-table__cell">
          {{ item.contractNumber }}
        </td>
        <td class="v-table__cell">
          {{ item.russianFullName }}
        </td>
        <td
          class="v-table__cell d-flex align-items-center justify-content-center"
        >
          <EditButton
            class="me-2"
            :disabled="!editable"
            @click="$emit('edit', index)"
            @mouseenter="editable ? (editIndexHover = index) : undefined"
            @mouseleave="editable ? (editIndexHover = undefined) : undefined"
          />
          <DeleteButton
            :disabled="!editable"
            @click="$emit('delete', index)"
            @mouseenter="editable ? (deleteIndexHover = index) : undefined"
            @mouseleave="editable ? (deleteIndexHover = undefined) : undefined"
          />
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import EditButton from "@/components/buttons/EditButton";
import DeleteButton from "@/components/buttons/DeleteButton";
import { dateToFormat } from "@/utils/DateUtils";

export default {
  name: "ResourcesExternalTable",
  components: { DeleteButton, EditButton },
  emits: {
    edit: Function,
    delete: Function,
  },
  props: {
    editable: Boolean,
    list: { type: Array, default: () => [] },
  },
  data() {
    return {
      editIndexHover: undefined,
      deleteIndexHover: undefined,
    };
  },
  computed: {
    resources() {
      return this.list.map((x) => ({
        ...x,
        date: dateToFormat({ date: x.date }),
      }));
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.resources-external-table__name-cell--edit-hover {
  color: $blue;
}

.resources-external-table__name-cell--delete-hover {
  color: $red;
}
</style>
