<template>
  <VModal class="justify-content-end" position="right" :show="show">
    <form @submit.prevent="onSubmit" autocomplete="off">
      <ModalContainer
        v-if="show"
        :title="title"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <TextInput
            label="Наименование организации"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            placeholder="Русский текст"
            v-model="organization.name.russian"
            name="organization.name.russian"
            root-class="mb-2"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="organization-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishName"
            v-model="organization.name.english"
            name="organization.name.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TextInput
            label="Контакт организации"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianContact"
            placeholder="Русский текст"
            v-model="organization.contact.russian"
            name="organization.contact.russian"
            root-class="mb-2"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="organization-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishContact"
            v-model="organization.contact.english"
            name="organization.contact.english"
            placeholder="Английский текст"
          />
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import VTypography from "@/components/ui/Typography";
import { getOrganizationInitialValues } from "@/helpers/OrganizationsHelpers";

export default {
  name: "OrganizationModal",
  components: {
    VTypography,
    PopoverContent,
    TextInput,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => getOrganizationInitialValues(),
    },
  },
  data() {
    return {
      errors: {},
      organization: getOrganizationInitialValues(),
    };
  },
  computed: {
    title() {
      const hasRussianName = !!this.initialValues?.name?.russian;

      return hasRussianName ? "Изменить организацию" : "Добавить организацию";
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue !== prevValue) {
        this.organization = { ...this.initialValues };
      }
    },
  },
  methods: {
    onSubmit(event) {
      this.errors = {};

      const hasRussianName = !!this.organization.name.russian;
      const hasEnglishName = !!this.organization.name.english;

      const hasRussianContact = !!this.organization.contact.russian;
      const hasEnglishContact = !!this.organization.contact.english;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }
      if (!hasRussianContact) {
        this.errors = {
          ...this.errors,
          russianContact: "Введите контакт на русском языке",
        };
      }

      if (!hasEnglishContact) {
        this.errors = {
          ...this.errors,
          englishContact: "Введите контакт на английском языке",
        };
      }

      if (
        hasRussianName &&
        hasEnglishName &&
        hasEnglishContact &&
        hasRussianContact
      ) {
        this.$emit("submit", this.organization);
        this.organization = getOrganizationInitialValues();

        event.target.reset();
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.organization-modal__popover {
  background-color: $gray3;
}
</style>
