<template>
  <VButton
    class="d-flex user-select-none align-items-center justify-content-center tabs-item"
    :class="rootClasses"
  >
    <VTypography class="tabs-item__title">{{ title }}</VTypography>
  </VButton>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";
import { FormStatus } from "@/constants/FormConstants";

export default {
  name: "TabsItem",
  components: { VButton, VTypography },
  emits: {
    visit: Function,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    active: Boolean,
    visited: Boolean,
    formStatus: String,
    status: {
      type: String,
      default: "not-filled",
      validator(value) {
        return ["filled", "not-filled", "not-fully-filled"].includes(value);
      },
    },
  },
  watch: {
    active(next, prev) {
      if (next !== prev) {
        this.$emit("visit");
      }
    },
  },
  computed: {
    rootClasses() {
      return {
        "tabs-item--active": this.active,
        "tabs-item--filled": this.visited && this.status === "filled",
        "tabs-item--not-filled": this.status === "not-filled",
        "tabs-item--not-fully-filled": this.status === "not-fully-filled",
        "tabs-item--inspection": this.formStatus === FormStatus.Inspection,
        "tabs-item--not-fully-filled-revision":
          this.formStatus === FormStatus.Revision &&
          this.status === "not-fully-filled",
        "tabs-item--not-filled-revision":
          this.formStatus === FormStatus.Revision &&
          this.status === "not-filled",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.tabs-item {
  @include transition(border-bottom-color);

  height: 26px;
  cursor: pointer;
  border-bottom: 2px solid $gray5;

  &:not(:first-child) {
    margin-left: 15px;
  }

  &:not(:last-child) {
    margin-right: 15px;
  }

  &:hover {
    &:not(.tabs-item--filled):not(.tabs-item--not-fully-filled) {
      border-bottom-color: $gray1;
    }

    & > .tabs-item__title {
      color: $gray1;
    }
  }
}

.tabs-item__title {
  @include transition(color);

  color: $gray4;
  white-space: nowrap;
}

.tabs-item--active {
  border-bottom-color: $gray1;

  & > .tabs-item__title {
    color: $gray1;
  }
}

.tabs-item--filled {
  border-bottom-color: $green;
}

.tabs-item--not-fully-filled,
.tabs-item--inspection {
  border-bottom-color: $yellow;
}

.tabs-item--not-filled-revision,
.tabs-item--not-fully-filled-revision {
  border-bottom-color: $orange;
}
</style>
